.intro-cont {
  display: flex;
  justify-content: center;
  /* justify-self: start; */
  align-content: center;
  /* flex-direction: column; */
  max-width: 80vw;
}

@media only screen and (min-width: 601px) {
  .intro-cont {

    flex-direction: row;
  
  }
}

@media only screen and (max-width: 600px) {
  .intro-cont {

    flex-direction: column;
  
  }
}
